@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap);
/** Basic CSS site boilerplate (Modification of http://getskeleton.com) */
/** Tags */
html {
	font-size: 62.5%;
	box-sizing: border-box;
	-webkit-text-size-adjust: 100%;
	   -moz-text-size-adjust: 100%;
	        text-size-adjust: 100%;
}
*,
*:before,
*:after {
	box-sizing: inherit;
}
body {
	font-size: 1.6em;
	line-height: 1.5;
	font-weight: 400;
	font-family: "Raleway", "HelveticaNeue", "Helvetica Neue", Helvetica, sans-serif;
	color: #222;
	min-height: 100vh;
	text-rendering: optimizeSpeed;
	padding: 0;
	margin: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
	margin-top: 0;
	margin-bottom: 2rem;
	font-weight: 300;
}
h1 {
	font-size: 4rem;
	line-height: 1.2;
	letter-spacing: -0.1rem;
}
h2 {
	font-size: 3.6rem;
	line-height: 1.25;
	letter-spacing: -0.1rem;
}
h3 {
	font-size: 3rem;
	line-height: 1.3;
	letter-spacing: -0.1rem;
}
h4 {
	font-size: 2.4rem;
	line-height: 1.35;
	letter-spacing: -0.08rem;
}
h5 {
	font-size: 1.8rem;
	line-height: 1.5;
	letter-spacing: -0.05rem;
}
h6 {
	font-size: 1.5rem;
	line-height: 1.6;
	letter-spacing: 0;
}
p {
	margin-top: 0;
}
a {
	color: #1eaedb;
	background-color: transparent;
}
a:hover {
	color: #0fa0ce;
}
a:not([class]) {
	-webkit-text-decoration-skip: ink;
	        text-decoration-skip-ink: auto;
}
button,
input[type="submit"],
input[type="reset"],
input[type="button"] {
	display: inline-block;
	height: 3.8rem;
	padding: 0 3rem;
	color: #555;
	text-align: center;
	font-size: 1.1rem;
	font-weight: 600;
	line-height: 3.8rem;
	letter-spacing: 0.1rem;
	text-transform: uppercase;
	text-decoration: none;
	white-space: nowrap;
	background-color: transparent;
	border-radius: 0.4rem;
	border: 1px solid #bbb;
	cursor: pointer;
	box-sizing: border-box;
}
button:hover,
input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
button:focus,
input[type="submit"]:focus,
input[type="reset"]:focus,
input[type="button"]:focus {
	color: #333;
	border-color: #888;
	outline: 0;
}
input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea,
select {
	height: 3.8rem;
	padding: 0.6rem 1rem;
	background-color: #fff;
	border: 1px solid #d1d1d1;
	border-radius: 0.4rem;
	box-shadow: none;
	box-sizing: border-box;
	background-color: gainsboro;
}
input[type="email"],
input[type="number"],
input[type="search"],
input[type="text"],
input[type="tel"],
input[type="url"],
input[type="password"],
textarea {
	-webkit-appearance: none;
	        appearance: none;
}
textarea {
	min-height: 6.5rem;
	padding-top: 0.6rem;
	padding-bottom: 0.6rem;
}
input[type="email"]:focus,
input[type="number"]:focus,
input[type="search"]:focus,
input[type="text"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
textarea:focus,
select:focus {
	border: 1px solid #33c3f0;
	outline: 0;
}
label,
legend {
	display: block;
	margin-bottom: 0.5rem;
	font-weight: 600;
}
fieldset {
	padding: 0;
	border-width: 0;
}
input[type="checkbox"],
input[type="radio"] {
	display: inline;
}
ul {
	list-style: circle inside;
}
ol {
	list-style: decimal inside;
}
ol,
ul {
	padding-left: 0;
	margin-top: 0;
}
ul ul,
ul ol,
ol ol,
ol ul {
	margin: 1.5rem 0 1.5rem 3rem;
	font-size: 90%;
}
li {
	margin-bottom: 1rem;
}
code {
	padding: 0.2rem 0.5rem;
	margin: 0 0.2rem;
	font-size: 90%;
	white-space: nowrap;
	background: #f1f1f1;
	border: 1px solid #e1e1e1;
	border-radius: 0.4rem;
}
pre > code {
	display: block;
	padding: 1rem 1.5rem;
	white-space: pre;
}
th,
td {
	padding: 1.2rem 1.5rem;
	text-align: left;
	border-bottom: 1px solid #e1e1e1;
}
th:first-child,
td:first-child {
	padding-left: 0;
}
th:last-child,
td:last-child {
	padding-right: 0;
}
button {
	margin-bottom: 1rem;
}
input,
textarea,
select,
fieldset {
	margin-bottom: 1.5rem;
}
pre,
blockquote,
dl,
figure,
table,
p,
ul,
ol,
form {
	margin-bottom: 2.5rem;
}
hr {
	margin-top: 3rem;
	margin-bottom: 3.5rem;
	border-width: 0;
	border-top: 1px solid #e1e1e1;
}
img {
	max-width: 100%;
	height: auto;
}
